.card-wrapp {
  border:1px solid silver;
  border-radius: 5px;
  padding: 5px;
}

.card-wrapp__image-wrapp {
  overflow: hidden;
  height: 200px;
}

.card-wrapp__image {
  cursor: pointer;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  transition: transform 0.5s ease;
}

.card-wrapp__image:hover {
  transition: transform 0.5s ease;
  transform: scale(1.2);
}

.hr {
  height: 5px;
}

.card-wrapp__commnets-count,
.card-wrapp__likes-count {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  cursor: pointer;
}

.footer {
  border-top:2px solid silver;
  padding: 5px 15px;
}

.modal-image {
  width: 100%;
  border-radius: 5px;
}